@import url("hubpage.css");

.search-page {
    min-height:500px;
}

.search-page h1 {
    padding: 0 1em 0.2em 1em;
    font-size: 2.4em;
}

.search-result-item {
    padding: 0 2.6em 2em 2.6em;
}

.search-result-item .icon {
    float:left;
    font-family: 'icons';
    font-size:1.6em;
    color:#999;
    margin-top:0.2em;
}

.search-result-item .content {
    margin-left: 4em;
    line-height: 120%;
}

.search-result-item .content div {
    font-size:0.86em;
}

.search-result-item .date-wide {
    float:right;
    color:#666;
    font-size:0.86em;
}

.search-result-item .date-narrow {
    color:#666;
    display:none;
    margin-top:0.6em;
}

.search-result-item li {
    display: inline-block;
    font-size:0.86em;
    margin-bottom:0.7em;
}

.search-result-item li a {
    color:#666;
}

.search-result-item li a:hover {
    color:#007bb8;
}

.search-result-item li::after {
    content: "\\";
    vertical-align: baseline;
    padding: 0 0.5em;
    color: #ababab;
}

.theme-night .search-result-item li:after {
    color: #6e6e6e
}

.search-result-item li:last-child {
    color: black;
}

.theme-night .search-result-item li:last-child {
    color: #828282;
}

.search-result-item li:last-child:before,.search-result-item li:last-child:after {
    content: ''
}

#search-paging
{
    float:right;
    position:relative;
    left:-50%;
    text-align:left;
    margin:3em 0;
}

#search-paging ul
{
    list-style:none;
    position:relative;
    left:50%;
}

#search-paging li
{
    float:left;
    position:relative;
}

#search-paging li a {
    border:solid 2px #fff;
    width: 40px;
    height: 40px;
    text-align: center;
    display:block;
    line-height:210%;
}

#search-paging li a:hover {
    background-color:#ededed;
}

#search-paging li a.selected {
    border-color:#999;
}

#search-paging li a.icon {
    font-family: "icons";
    font-size: 1.4em;
    line-height: 160%;
    color:#666;
}

#search-paging li a.icon:hover {
    color:#0070a7;
}

@media only screen and (max-width: 600px) {
    .search-page h1 {
        padding: 0 0.6em 0.2em 0.6em;
        font-size: 1.9em;
    }

    .search-result-item {
        padding: 0 1.2em 2em 1.2em;
    }

    .search-result-item .content {
        margin-left: 3em;
    }

    .search-result-item .date-wide {
        display:none;
    }

    .search-result-item .date-narrow {
        display:block;
    }

    #search-paging
    {
        margin:2em 0;
    }

    #search-paging li.edge {
        display:none
    }
}